html{height:100%}html #app{color:#2c3e50;width:80%;margin:10px auto;font-family:Source Sans Pro, Helvetica, sans-serif;text-align:center}@media screen and (max-width: 600px){html #app{width:96%}}html #app .file-server{z-index:1}@media screen and (max-width: 600px){html #app .file-server{font-size:12px}}html #app .file-server span{height:30px;line-height:30px;display:block;float:left}html #app .file-server .server-address{float:left}html #app .file-server .server-qrcode{float:right}html #app .file-server .server-qrcode span:last-child{float:right;width:30px;height:30px;line-height:30px;background-image:url("./assets/qrcode.png");background-size:20px 20px;text-align:center;background-repeat:no-repeat;margin-top:3px}html #app #popup{width:300px;height:300px;z-index:99;background:rgba(0,0,0,0.6);position:absolute;left:50%;top:50%;margin-left:-150px;margin-top:-150px}html #app #popup img{width:80%;margin-top:10%;margin-left:10%}html #app .file-list{margin-top:30px}html #app .file-list .list-top{height:45px;line-height:45px;background:#4078c0;border:0.5px solid #ccc;border-radius:3px;color:#fff;text-align:left;padding-left:10px}html #app .file-list .list-body .file-specifc{height:40px;line-height:40px;border-bottom:0.5px solid rgba(44,62,80,0.5)}html #app .file-list .list-body .file-specifc span{display:block;line-height:40px;width:25%;height:40px;float:left}html #app .file-list .list-body .file-specifc span:first-child{padding-left:5px;width:48%;text-align:left}html #app .file-list .list-body .file-specifc .download{display:block;float:left;width:25%;height:40px;background:url("./assets/download.png");background-size:30px 30px;background-repeat:no-repeat;cursor:pointer;opacity:0.6;background-position:center}html #app .file-list .list-body .file-specifc .download:hover{opacity:1}html #app .file-list .list-body .file-specifc .delete{background:url("./assets/delete.png");background-size:30px 30px;background-repeat:no-repeat;cursor:pointer;opacity:0.6;background-position:center}html #app .file-list .list-body .file-specifc .delete:hover{opacity:1}html #app .upload{position:relative;height:30px;width:200px;margin-top:30px}html #app .upload .select{float:left;width:100px}html #app .upload .select input{position:absolute;top:0px;left:0px;height:30px;line-height:30px;width:100px;z-index:99;opacity:0}html #app .upload .select span{position:absolute;top:0px;left:0px;height:30px;line-height:30px;width:100px;z-index:1;background:rgba(60,60,60,0.7);border:0px;color:#fff;border-radius:3px;cursor:pointer}html #app .upload .upload-file{width:80px;height:30px;line-height:30px;border:0px;color:#fff;border-radius:3px;background:#4078c0;float:right;cursor:pointer}html .clear{clear:both;width:0px;height:0px}



